.cover {
  width: 98%;
  position: relative;
  margin: 0 auto;
  top: -35px;
}
.im {
  width: 50%;
  height: auto;
  margin: 0 auto;
  opacity: 0.9;
}
.foto {
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: 50%;
}

.parent {
  width: 50%;
  height: 200px;
  left: 0px;
}
.box {
  width: 1px;
  height: 1px;
  background-color: rgb(252, 249, 252);
  font-size: 20px;
  left: 5%;
  top: 20px;
  position: absolute;
  -webkit-transition-property: width height background-color font-size left top
    color;
  -webkit-transition-duration: 6s;
  -webkit-transition-timing-function: ease-in;
  transition-property: width height background-color font-size left top color;
  transition-duration: 6s;
  transition-timing-function: ease;
  align-self: center;
  moz-transform: scale(1.5) rotate(90deg);
}
.box1 {
  width: 100%;
  height: 180px;
  color: teal;
  font-size: 18px;
  top: 200px;
  position: absolute;
  -webkit-transition-property: width height background-color font-size left top
    color;
  moz-transform: scale(1.5) rotate(90deg) translate(50px, -50px);
  -webkit-transition-duration: 6s;
  -webkit-transition-timing-function: ease-out;
  transition-property: width height background-color font-size left top color;
  transition-duration: 6s;
  transition-timing-function: ease;
}

@import url("https://fonts.googleapis.com/css?family=Megrim&display=swap");

.title {
  text-align: center;
  color: rgb(243, 30, 101);
  font-size: 40px;
  font-weight: 600;
  font-family: "Megrim", cursive;
  padding: 10px;
  position: relative;
}

.location {
  padding-top:10px;
  text-align: center;
  color: rgb(243, 30, 101);
  font-size: 30px;
  font-weight: 900;
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
  z-index: 4;
  position: relative;
}
