.call-out {
	position: relative;
	margin: .5em;
	width: 100%;
	max-width: 250px;
	text-align: center;	
    overflow: hidden;   
}

/* display .call-out as 1:1 ratio */
.call-out:before {
	display: block;
	content: '';
	margin-top: 100%;
}

.links {
